<script setup>
import { ref, onMounted, onUnmounted } from 'vue'
import AiAgentButton from '@/components/ai/AiAgentButton.vue'

const video = ref(null)
const videoWrapper = ref(null)
const bgVideo = ref(null)

// Генерируем статичные позиции для кругов один раз
const floatingDots = Array.from({ length: 16 }, () => ({
  x: `${Math.random() * 100}%`,
  y: `${Math.random() * 100}%`,
  delay: `${Math.random() * 4}s`,
  size: `${4 + Math.random() * 35}px`,
  duration: `${4 + Math.random() * 4}s`
}))

const slides = [
  { url: 'https://solanabitcat.com/data/universe-section/universe-ai-1.png', alt: 'AI Agents 1' },
  { url: 'https://solanabitcat.com/data/universe-section/universe-ai-2.png', alt: 'AI Agents 2' },
  { url: 'https://solanabitcat.com/data/universe-section/universe-game-1.png', alt: 'Game 1' },
  { url: 'https://solanabitcat.com/data/universe-section/universe-game-2.png', alt: 'Game 2' },
  { url: 'https://solanabitcat.com/data/universe-section/universe-dao.png', alt: 'DAO' }
]

const slideGroups = [
  {
    title: 'AI Agents',
    description: 'Create and customize your own AI agents for various tasks',
    link: '/ai-agents',
    buttonText: 'Learn More',
    duration: 10000 // 2 slides * 5000ms
  },
  {
    title: 'BITCAT Game',
    description: 'Explore the exciting world of BITCAT gaming',
    link: '/hide-seek',
    buttonText: 'Play Now',
    duration: 10000 // 2 slides * 5000ms
  },
  {
    title: 'BITCAT DAO',
    description: 'Join our DAO Voting and be part of decision making',
    link: '/dao',
    buttonText: 'Join DAO',
    duration: 10000 // 1 slide * 10000ms
  }
]

const currentSlide = ref(0)
const currentGroup = ref(0)
let slideInterval = null

const nextSlide = () => {
  currentSlide.value = (currentSlide.value + 1) % slides.length
  if (currentSlide.value === 0) {
    // Reset to first slide smoothly
    setTimeout(() => {
      currentSlide.value = 0
    }, 0)
  }
}

const nextGroup = () => {
  currentGroup.value = (currentGroup.value + 1) % slideGroups.length
}

const startSlideshow = () => {
  slideInterval = setInterval(() => {
    if (currentGroup.value === 0) {
      // AI Agents group
      if (currentSlide.value < 1) {
        nextSlide()
      } else {
        nextSlide()
        nextGroup()
      }
    } else if (currentGroup.value === 1) {
      // Game group
      if (currentSlide.value < 3) {
        nextSlide()
      } else {
        nextSlide()
        nextGroup()
      }
    } else {
      // DAO group (10 seconds)
      setTimeout(() => {
        nextSlide()
        nextGroup()
      }, 10000)
    }
  }, 5000)
}

const goToGroup = (groupIndex) => {
  currentGroup.value = groupIndex
  currentSlide.value = groupIndex * 2
  
  // Reset interval
  if (slideInterval) {
    clearInterval(slideInterval)
    startSlideshow()
  }
}

onMounted(() => {
  // Start background video immediately
  if (bgVideo.value) {
    bgVideo.value.addEventListener('loadedmetadata', () => {
      playVideo(bgVideo.value)
    })
  }

  if (video.value) {
    video.value.addEventListener('loadedmetadata', () => {
      playVideo(video.value)
    })
  }

  startSlideshow()
})

onUnmounted(() => {
  if (slideInterval) {
    clearInterval(slideInterval)
  }
})

const playVideo = async (videoElement) => {
  if (!videoElement) return
  try {
    await videoElement.play()
  } catch (error) {
    console.log('Video playback failed:', error)
  }
}
</script>

